import React from 'react'

const Display = (props) => {
    return (
        <div className='display w-100'>
            <input type='text' className='plain-input w-100 font-green' style={{ backgroundColor: 'transparent', textAlign: 'right' }} value={props.data} onChange={(e) => props.setData(e.target.value)} />
        </div>
    )
}

export default Display